



























import Vue from "vue";

export default Vue.extend({
  name: "App",

  mounted() {
    const theme = localStorage.themeDark;
    if (theme === "true") {
      this.$vuetify.theme.dark = true;
    }
  },
  data: () => ({
    activeTab: "/",
    tabs: [
      { name: "2048", route: "/" },
      { name: "Todo", route: "/todo" },
      { name: "Posts", route: "/posts" },
      { name: "Snake", route: "/snake" }
    ],
    last: { name: "About", route: "/about" }
  })
});
